<template>
	<scroll-view class='goods_list' :scroll-y="true" :scroll-with-animation="true" @refresherrefresh="onRefresh"
		:lower-threshold="60" :refresher-enabled='true' :refresher-triggered="triggered" refresher-default-style="none"
		:refresher-threshold="60" refresher-background="#2789FF" style="height: 100vh;" @scrolltolower="loadMore">
		<view slot="refresher" class="refresh-container"
			style="width: 100%; height: 60px;  display: flex; align-items: center;justify-content: center;color:#FFF">
			<view class="view1"
				style="position: absolute; display: flex; align-items:center;justify-content:center;width:100%;flex-direction: column;">
				<!-- <image src="../../static/img/icon.png" mode="" style="width: 50rpx;height: 40rpx;"></image> -->
				<text style="color:#F3F4F6">{{loadingText}}</text>
			</view>
		</view>
		<slot></slot>
	</scroll-view>
</template>

<script>
	export default {
		data() {
			return {
				loadingText: "下拉刷新!",
				triggered: false, //设置当前下拉刷新状态，true 表示下拉刷新已经被触发，false 表示下拉刷新未被触发
			}
		},
		props: {
			loadingTextS: {
				type: String,
				default: () => {
					return "下拉刷新！"
				}
			}
		},
		methods: {
			onRefresh(){
				if (!this.triggered) {
					this.triggered = true;
					this.loadingText ="刷新中..."
					// vibrateLong
					wx.vibrateShort({
						type:"heavy",
					})
					setTimeout(()=>{
						this.triggered = false
						this.loadingText ="下拉刷新！"
					},1500)
				}
			},
			loadMore(){
				console.log("loadMore")
			}
		}
	}
</script>

<style lang="scss">

</style>
